/*----------------------------------------------------------------
    General Rules
----------------------------------------------------------------*/

::selection {
    background: #FD685B;
    color: #fff;
}

::-moz-selection {
    background: #FD685B;
    color: #fff;
}

::-webkit-input-placeholder {
    color: #b3b3b3;
}

:-moz-placeholder {
 /* Firefox 18- */
    color: #b3b3b3;
}

::-moz-placeholder {
  /* Firefox 19+ */
    color: #b3b3b3;
}

:-ms-input-placeholder {
    color: #b3b3b3;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

html {
    font-size: 100%;
 /* font-size: 16px; */;
}

body {
    background-color: #fbfbfb;
    color: #333;
    font-size: 1.16em; /* font-size: 18px; */
    font-family: 'Open Sans', sans-serif;
    line-height: 1.8;
    font-weight: 300;
}

h1, h2, h3, h4, h5, h6 {
    color: #222;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.34;
    text-rendering: auto; /* Android browser */
    font-weight: 400;
}

a {
    color: #222;
    text-decoration: underline;
}

.csstransitions a {
    transition: background-color 0.1s linear, color 0.1s linear;
}

input[type=""],
input:not([type]),
input[type="text"],
input[type="password"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
textarea,
textarea.plain {
    display: block;
    box-sizing: border-box;
    margin-bottom: 1em;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.333em;
    color: #333;
}

textarea:focus,
input:focus {
    outline: 0;
}

textarea:not([type="button"]):hover,
input:not([type="button"]):hover {
    border-color: #cad1d9 !important;
}

textarea:not([type="button"]):focus,
textarea:not([type="button"]):active,
input:not([type="button"]):focus,
input:not([type="button"]):active {
    border-color: #88d3ff !important;
}

img {
    display: block;
}

.centertxt {
    text-align: center;
}

#main {
    position: relative;
    z-index: 10;
    overflow: hidden;
    background-color: #fbfbfb;
}

section {
    overflow-x: hidden;
}

.last-special {
    margin-right: 0% !important;
}

/*----------------------------------------------------------------
    Perfect Fourth (http://type-scale.com)
----------------------------------------------------------------*/

h1 {
    font-size: 2.369em;
}

h2 {
    font-size: 1.777em;
}

h3 {
    font-size: 1.333em;
}

h4 {
    font-size: 1em;
}

h5 {
    font-size: 0.75em;
}

h6 {
    font-size: 0.563em;
}

.row-content.buffer,
.row-content.buffer-top {
    padding-top: 3.8%;
 /* This overwrites a layers.css rule */;
}


/*----------------------------------------------------------------
    Loaders
----------------------------------------------------------------*/

.landing {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10001;
    width: 100%;
    height: 100%;
}

.loader {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 6.250em;
    height: 6.250em;
    animation: rotate 2.4s linear infinite;
}

.spinner {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto auto;
    width: 5em;
    height: 2em;
    text-align: center;
    user-select: none;
}

.spinner > div {
    display: inline-block;
    width: 1em;
    height: 1em;
    border-radius: 100%;
    background-color: #fff;
    animation: bouncedelay 1.4s infinite ease-in-out;
    /* Prevent first frame from flickering when animation starts */
    animation-fill-mode: both;
}

.landing-slider .spinner > div {
    background-color: rgba(255,255,255,0.5);
}

.landing-els .spinner > div {
    background-color: rgba(0,0,0,0.2);
}

.spinner .bounce1 {
    margin-right: 0.5em;
    animation-delay: -0.32s;
}

.spinner .bounce2 {
    margin-right: 0.5em;
    animation-delay: -0.16s;
}

@keyframes bouncedelay {
    0%, 80%, 100% {
        transform: scale(0.0);
    }

    40% {
        transform: scale(1.0);
    }
}


/*----------------------------------------------------------------
    Header menu
----------------------------------------------------------------*/

header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    padding: 0.5rem 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.97);
