/*----------------------------------------------------------------
    onScreen animations
----------------------------------------------------------------*/

.slogan.animation.onscreen,
.call-to-action .animation.onscreen,
.side-mockup.left-mockup.animation.onscreen {
    opacity: 1;
    transition-delay: 0.2s;
    transform: translateX(0);
    animation: lfade .65s ease forwards;
}

.side-mockup.right-mockup.animation.onscreen {
    opacity: 1;
    transition-delay: 0.2s;
    transform: translateX(0);
    animation: rfade .65s ease forwards;
}

.count-subject.subject-on {
    opacity: 1;
    transform: translateX(0);
    animation: smalllfade .65s ease forwards;
}

.animation.onscreen .section-title{
  opacity: 0;
  animation-delay: .6s;
  transform: translateY(3em);
  animation: mcaption .85s ease forwards;
}

.animation.onscreen .section-subtitle {
  opacity: 0;
  animation-delay: .8s;
  transform: translateY(3em);
  animation: mcaption .85s ease forwards;
}

.animation.onscreen .service-boxes,
.animation.onscreen .testimonial-slider,
.animation.onscreen .client-logos {
  opacity: 0;
  animation-delay: 1s;
  transform: translateY(3em);
  animation: mcaption .85s ease forwards;
}

.owl-item .caption h2,
.owl-item .caption p,
.owl-item .caption .button,
.owl-item .caption-mockup h2,
.owl-item .caption-mockup p,
.owl-item .caption-mockup .button,
.owl-item .photocaption,
.side-mockup.left-mockup.animation,
.side-mockup.right-mockup.animation,
.slogan.animation,
.call-to-action .animation {
    opacity: 0;
}

.owl-item.active .photocaption {
    opacity: 0;
    transform: translateX(3em);
    animation: photocaption .65s ease forwards;
    animation-delay: .6s;
}

.no-cssanimations .owl-item.active .photocaption {
    -ms-transform: translateX(0);   
}

.owl-item.active .intro-mockup img {
    opacity: 0;
    transform: translateY(3em);
    animation: mcaption .65s ease forwards;
    animation-delay: .8s;
}

.owl-item.active .caption h2,
.owl-item.active .caption-mockup h2 {
    opacity: 0;
    animation: fade .65s ease forwards;
    animation-delay: .6s;
}

.owl-item.active .caption p,
.owl-item.active .caption-mockup p {
    opacity: 0;
    animation: fade .65s ease forwards;
    animation-delay: .8s;
}

.owl-item.active .caption .button,
.owl-item.active .caption-mockup .button {
    opacity: 0;
    animation: fade .65s ease forwards;
    animation-delay: 1s;
}

.no-cssanimations .owl-item.active .photocaption,
.no-cssanimations .owl-item.active .caption h2,
.no-cssanimations .owl-item.active .caption-mockup h2,
.no-cssanimations .owl-item.active .caption p,
.no-cssanimations .owl-item.active .caption-mockup p,
.no-cssanimations .owl-item.active .caption .button,
.no-cssanimations .owl-item.active .caption-mockup .button {
    opacity: 1;
}

@keyframes fade {
    to {
        opacity: 1;
    }
}

@keyframes photocaption {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes mcaption {
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes lfade {
    from {
        opacity: 0;
        transform: translateX(-3em);
    }
}

@keyframes smalllfade {
    from {
        opacity: 0;
        transform: translateX(-1em);
    }
}

@keyframes rfade {
  from {
    opacity: 0;
    transform: translateX(3em);
  }
}

@keyframes bfade {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


